import React, { Component } from 'react';

export function TurnContent(cont) {
    let temp;
    const numSym = cont.selectedBlock.indexOf('_');
    for(let block in cont.blocks){
        const str = block.substr(0, numSym+1);
        console.log(str);
        // if(block.substr(0, numSym) === cont.selectedBlock)
    }
    return(
        <div className="contentTool">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div className="noneBorder"></div>
        </div>
    );
}

export default class Turn extends Component{

    selectItem(){
        const { setCurrentItem } = this.props.toolsActions;
        setCurrentItem('turn');
    }

    render(){
        let temp = 'toolsItem turn';
        if(this.props.tools.currentItem === 'turn')
            temp += ' active noneBorder';
        return(
            <div
                className = {temp}
                onClick={this.selectItem.bind(this)}
            >
            </div>
        );
    }
}